import React from 'react';

import ReactCarousel from '@brainhubeu/react-carousel';
import '@brainhubeu/react-carousel/lib/style.css';

import data from './data.json';

import styles from './carousel.module.scss';

export const Carousel = ({ value, onChange }) => {
  const r = require.context('./', false, /\.(png|jpe?g)$/);
  
  const images = data.map(({ image: url }) => {
    return <img src={r(url)} alt="" /> ; 
  });
  
  return (
    <div className={styles.carouselContainer}>
      <ReactCarousel fastSwipe arrows centered value={value} onChange={onChange} className={styles.carousel}>
        { images }
      </ReactCarousel>
    </div>
  );
}